// Tables
//
// The `<table>` element displays tabular data in rows, columns, and cells.
//
// Markup: tables-table.twig
//
// Weight: 1
//
// Style guide: base.table

// @todo We shouldn't need to import this here, but it won't compile without it.
@import '../abstracts/mixins';

table {
  @include AU-space('margin-bottom', 1unit);
  border-collapse: collapse;
  border: $health-borderize--thin;

  tr:nth-child(even) {
    background-color: $AU-color-background;
  }

  &.table__no-banding {
    tr:nth-child(even) {
      background-color: transparent;
    }
  }

  td,
  th {
    @include AU-media('md') {
      @include AU-space('padding', .4unit);
      font-size: initial;
    }

    border-bottom: $health-borderize--thin;
    @include AU-space('padding', .25unit);
    text-align: left;
    vertical-align: top;

  }

  thead {
    border-bottom: 2px solid $AU-color-foreground-border;
  }

  min-width: 43em;
  @include AU-media( md ) {
    min-width: auto;
  }
}

.responsive-table-wrapper {
  overflow: scroll;
  @include AU-media( md ) {
    overflow: auto;
  }
}

.paragraphs-item-content-table {
  .field-name-field-title {
    font-weight: bold;
    @include AU-space('padding-bottom', .75unit);
  }
  .field-name-field-table-source {
    @extend %minor-text;
    @include AU-space('margin-bottom', 1unit);
  }
}