// Health alerts region
.region-alerts {
  background-color: $AU-color-error;
  border-top: 6px solid $AU-color-error;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 600;
  transition: all .3s ease-in-out;
  .close-toggle {
    position: absolute;
    @include AU-space('top', .5unit);
    right: 12px; // sisze of spacing
  }
}

// When close button is clicked hide the close button.
.health-alert-inactive .region-alerts {
  bottom: -100%;
}

