// Resizable textarea
//
// A textarea that can be resized with a "grippie" widget.
//
// Markup: resizable-textarea.twig
//
// Style guide: forms.resizable-textarea

%au-resizable-textarea {
  width: 100%;
  vertical-align: bottom;

  &__grippie {
    background: url('../sass/forms/resizable-textarea/grippie.png') no-repeat center 2px color(resizable-grippie);
    border: 1px solid color(border);
    border-top-width: 0;
    cursor: s-resize;
    height: 9px;
    overflow: hidden;
  }
}


